body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.App {
  text-align: center;
  min-height: 100vh; }

.App-logo {
  height: 12vmin; }

.App-header {
  background-color: #282c34;
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-Content {
  padding: 12px 30px; }

.theme-toggle {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-items: center; }

.theme-toggle-btn {
  padding-top: 10px; }

.light {
  color: #000000;
  background: #eeeeee; }

.dark {
  color: #ffffff;
  background: #222222; }

.App-link {
  color: #09d3ac; }

.nav {
  display: flex;
  list-style: none;
  margin-bottom: 20px; }

.nav li {
  margin-left: 10px;
  margin-bottom: 10px; }

.outlet {
  display: flex;
  align-items: center;
  place-content: center; }

.router-link {
  text-decoration: none;
  padding: 10px; }

.router-link:hover {
  border-radius: 5px; }

.selected {
  text-decoration: none;
  color: #3f51b5;
  padding-bottom: 5px;
  border-bottom: 2px solid #3f51b5; }

